import { getTemplate, getSlotContent, getStringAttr  } from '@/components/template'

var handle = function(_attr, _slots) {
    //
    let attributes = {
        },
        slots = {
            default: []
        }

    //
    Object.assign(slots, _slots)
    Object.assign(attributes, _attr)

    //


    //
    var subContent = getSlotContent(slots) || '&nbsp;'
    //slot
    if (attributes.slot && attributes.slot!=='default') {
        attributes.slot = {
            type: 'text',
            value: attributes.slot
        }
    } else {
        attributes.slot = {
            type: 'text',
            value: ''
        }
    }

    //
    let stringAttr = getStringAttr(attributes)
    let template = `<mu-tbody
                    	${stringAttr}>
                        ${subContent}
                    </mu-tbody>`

    return { template, attributes, slots }
}
export default handle



